.create__quiz__form .questions__number{
  display:inline-block;
  position:relative;
  margin-left:20px
}

.create__quiz__form .questions__number .increase__button,.create__quiz__form .questions__number .decrease__button{
  position:absolute;
  height:25px;
  width:25px;
  cursor:pointer
}

.create__quiz__form .questions__number span{
  font-size:24px
}

.create__quiz__form .questions__number .increase__button{
  right:-25px;
  top:5px
}

.create__quiz__form .questions__number .decrease__button{
  -webkit-transform:rotate(180deg);
  transform:rotate(180deg);
  left:-25px;
  top:5px
}

.create__quiz__form .remove__question__button{
  position:relative;
  padding:18px;
  border-radius:50%;
  transition:all .2s
}

.create__quiz__form .remove__question__button::before{
  content:"";
  width:60%;
  height:1px;
  position:absolute;
  background:#8f8f8f;
  left:50%;
  bottom:50%;
  -webkit-transform:translate(-50%, -50%) rotate(45deg);
  transform:translate(-50%, -50%) rotate(45deg);
  transition:all .2s
}

.create__quiz__form .remove__question__button::after{
  content:"";
  width:60%;
  height:1px;
  position:absolute;
  background:#8b8b8b;
  left:50%;
  bottom:50%;
  -webkit-transform:translate(-50%, -50%) rotate(-45deg);
  transform:translate(-50%, -50%) rotate(-45deg);
  transition:all .2s
}

.create__quiz__form .remove__question__button:hover{
  background-color:rgba(253,251,109,.301)
}

.create__quiz__form .remove__question__button:hover::before,.create__quiz__form .remove__question__button:hover::after{
  width:50%
}

.create__quiz__form input[type=radio]{
  -webkit-appearance:none;
  appearance:none;
  background-color:#fff;
  font:inherit;
  color:rgba(116,108,1,.692);
  min-width:22px;
  max-width:22px;
  min-height:22px;
  max-height:22px;
  border:3px solid rgba(116,108,1,.692);
  border-radius:50%;
  display:grid;
  place-content:center
}

.create__quiz__form input[type=radio]::before{
  content:"";
  border-radius:50%;
  width:10px;
  height:10px;
  -webkit-transform:scale(0);
  transform:scale(0);
  transition:120ms -webkit-transform ease-in-out;
  transition:120ms transform ease-in-out;
  transition:120ms transform ease-in-out, 120ms -webkit-transform ease-in-out;
  box-shadow:inset 1em 1em rgba(116,108,1,.692)
}

.create__quiz__form input[type=radio]:checked::before{
  -webkit-transform:scale(1);
  transform:scale(1)
}

