.modal{
  left:0;
  position:fixed;
  background:rgba(0,0,0,.39);
  display:inline-block;
  top:0;
  height:100%;
  width:100%;
  z-index:100;
  opacity:1;
  transition:all .3s ease
}

.modal .modal__wrap{
  background-color:#fff;
  border-radius:5px;
  padding:50px 25px;
  position:relative;
  height:80%;
  width:60%;
  top:50%;
  left:50%;
  -webkit-transform:translate(-50%, -50%) scale(1);
  transform:translate(-50%, -50%) scale(1);
  transition:all .2s ease;
  overflow-y:auto;
  overflow-x:hidden
}

.modal .modal__wrap::-webkit-scrollbar{
  width:.5em
}

.modal .modal__wrap::-webkit-scrollbar-track{
  box-shadow:inset 0 0 6px rgba(0,0,0,.3)
}

.modal .modal__wrap::-webkit-scrollbar-thumb{
  background-color:rgba(129,129,129,.521)
}

.modal .modal__wrap .close{
  position:absolute;
  right:15px;
  top:15px;
  padding:18px 20px;
  background-color:#e9e9e9;
  border:none;
  transition:all .2s
}

.modal .modal__wrap .close::before{
  content:"";
  width:60%;
  height:1px;
  position:absolute;
  background:#8f8f8f;
  left:50%;
  bottom:50%;
  -webkit-transform:translate(-50%, -50%) rotate(45deg);
  transform:translate(-50%, -50%) rotate(45deg)
}

.modal .modal__wrap .close::after{
  content:"";
  width:60%;
  height:1px;
  position:absolute;
  background:#8b8b8b;
  left:50%;
  bottom:50%;
  -webkit-transform:translate(-50%, -50%) rotate(-45deg);
  transform:translate(-50%, -50%) rotate(-45deg)
}

.modal .modal__wrap .close:hover{
  background-color:rgba(0,0,0,.644)
}

.modal .modal__wrap .close:hover::before,.modal .modal__wrap .close:hover::after{
  background-color:#fff
}

@media(max-width: 800px){
  .modal__wrap{
    width:100% !important;
    height:100% !important
  }
}

.disableModal{
  opacity:0;
  z-index:-10
}

.disableModal .modal__wrap{
  -webkit-transform:translate(-50%, -50%) scale(0);
  transform:translate(-50%, -50%) scale(0)
}

